*{
  margin:0;
  padding:0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #003;
  padding-top: 50px;
  color: white;
  text-align: center;
}

.Cell {
  height: 100px;
  width: 100px;
  background-color: darkgray;
  transition: background-color .8s;
}

.Cell-lit {
  background-color: #3addd3;
}
@-webkit-keyframes fast-blink-animation{
  0%{
    opacity: 1;
  }
  25%{
    opacity:50%;
  }
  50%{
    opacity: 1;
  }
  75%{
    opacity:50%;
  }
  100%{
    opacity: 1;
  }
}
@keyframes fast-blink-animation{
  0%{
    opacity: 1;
  }
  25%{
    opacity:50%;
  }
  50%{
    opacity: 1;
  }
  75%{
    opacity:50%;
  }
  100%{
    opacity: 1;
  }
}
@-webkit-keyframes slow-blink-animation{
  0%{
    opacity: 1;
  }

  50%{
    opacity: 50%;
  }

  100%{
    opacity: 1;
  }
}
@keyframes slow-blink-animation{
  0%{
    opacity: 1;
  }

  50%{
    opacity: 50%;
  }

  100%{
    opacity: 1;
  }
}

.Board{
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.Board table{
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  box-shadow:  6px 12px 26px 1px #881cdb, -4px -8px 20px 1px  #4af1e6,-15px 0px 26px 1px #fd55d6,1px 12px 26px 1px #e7b7f6;
   
   }

.Board-headings{
  display: -webkit-flex;
  display: flex;
  margin-top: 10%;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap:1em;
  gap:1em;
  margin-bottom: 2em;
}
.Board-heading,.Board-sub-heading{
 
  font-size: 8em;
font-family: 'MyWebFont';
  src: url(/lights-out/static/media/Potra.96a10766.otf) format('woff');
  color: #f87117;
  text-shadow: 3px 0px 11px rgb(247, 153, 32);
  -webkit-animation: fast-blink-animation 2s linear ;
          animation: fast-blink-animation 2s linear ;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.Board-sub-heading{
color: #2bc4f3;
text-shadow: 3px 0px 11px rgb(32, 247, 236);
-webkit-animation:slow-blink-animation 2s linear ;
        animation:slow-blink-animation 2s linear ;
-webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;

}

@font-face {
  font-family: 'MyWebFont';
  src: url(/lights-out/static/media/Potra.96a10766.otf); 
  
}
.App {
  text-align: center;
  margin-right: auto;

}
